@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
.loader.flex-center {
    background-color: #fff;
    flex-direction: column;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 100;
}

.loader.hide { -webkit-animation: hide-loader 0.5s ease-out forwards; animation: hide-loader 0.5s ease-out forwards; }

@-webkit-keyframes hide-loader {
    0% { transform: translateX(0); }
    100% { transform: translateX(100%); }
}

@keyframes hide-loader {
    0% { transform: translateX(0); }
    100% { transform: translateX(100%); }
}

.app-name {
    -webkit-animation: show-app-name 0.3s ease-in 3.8s 1 forwards;
            animation: show-app-name 0.3s ease-in 3.8s 1 forwards;
    color: #555;
    letter-spacing: 2px;
    position: relative;
    opacity: 0;
    text-transform: uppercase;
}

@-webkit-keyframes show-app-name {
    0% { opacity: 0; }
    100% { opacity: 1; }
}

@keyframes show-app-name {
    0% { opacity: 0; }
    100% { opacity: 1; }
}

.cloud-wrapper {
    background: linear-gradient(to bottom, #555, #fff);
    border-radius: 50%;
    position: relative;
    width: 12em;
    height: 12em;
}

.cloud {
    -webkit-animation: cloud-bounce 0.7s ease-out 1s, hide-cloud 0.1s linear 3.8s forwards;
            animation: cloud-bounce 0.7s ease-out 1s, hide-cloud 0.1s linear 3.8s forwards;
    -webkit-animation-iteration-count: 3, 1;
            animation-iteration-count: 3, 1;
    position: absolute;
    width: 10em;
    z-index: 4;
}

@-webkit-keyframes cloud-bounce {
    0% { transform: scale(1.2, 0.8) translateY(0); }

    10%, 80% { transform: scale(1) translateY(-2em); }

    50% { transform: scale(1) translateY(-3em); }

    100% { transform: scale(1.1, 0.9) translateY(0); }
}

@keyframes cloud-bounce {
    0% { transform: scale(1.2, 0.8) translateY(0); }

    10%, 80% { transform: scale(1) translateY(-2em); }

    50% { transform: scale(1) translateY(-3em); }

    100% { transform: scale(1.1, 0.9) translateY(0); }
}

@-webkit-keyframes hide-cloud {
    0% { visibility: visible; }
    100% { visibility: hidden; }
}

@keyframes hide-cloud {
    0% { visibility: visible; }
    100% { visibility: hidden; }
}

.cloud-bite {
    -webkit-animation: cloud-bite 0.1s linear 3.8s forwards;
            animation: cloud-bite 0.1s linear 3.8s forwards;
    visibility: hidden;
    width: 10em;
    z-index: 5;
}

@-webkit-keyframes cloud-bite {
    0% { visibility: hidden; }
    100% { visibility: visible; }
}

@keyframes cloud-bite {
    0% { visibility: hidden; }
    100% { visibility: visible; }
}

.cloud-shadow {
    -webkit-animation: animate-shadow 0.7s ease-out 1s;
            animation: animate-shadow 0.7s ease-out 1s;
    -webkit-animation-iteration-count: 3;
            animation-iteration-count: 3;
    background-color: rgba(0,0,0,0.4);
    border-radius: 50%;
    position: absolute;
    top: 70%;
    width: 8em;
    height: 1em;
    z-index: 3;
}

@-webkit-keyframes animate-shadow {
    0% { transform: scale(1); }

    10%, 80% { transform: scale(0.6); }

    50% { transform: scale(0.5); }

    100% { transform: scale(1); }
}

@keyframes animate-shadow {
    0% { transform: scale(1); }

    10%, 80% { transform: scale(0.6); }

    50% { transform: scale(0.5); }

    100% { transform: scale(1); }
}

.cloud-banner {
    border-radius: 50%;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.cloud-banner::after {
    background:linear-gradient(to top, #fff 50%, transparent);
    content: '';
    position: absolute;
    bottom: 0;
    height: 20%;
    width: 100%;
}

.cloud-banner img {
    -webkit-animation: animate-clouds 3s linear infinite;
            animation: animate-clouds 3s linear infinite;
    position: absolute;
    bottom: 10%;
    max-width: 100%;
    z-index: 1;
}

.cloud-banner img:nth-child(2) { -webkit-animation-duration: 6s; animation-duration: 6s; }

.cloud-banner img:nth-child(3) { -webkit-animation-duration: 9s; animation-duration: 9s; }

.cloud-banner img:nth-child(4) { -webkit-animation-duration: 12s; animation-duration: 12s; }

.cloud-banner img:nth-child(5) { -webkit-animation-duration: 15s; animation-duration: 15s; }

@-webkit-keyframes animate-clouds {
    0% { transform: translateX(-100%); }
    100% { transform: translateX(100%); }
}

@keyframes animate-clouds {
    0% { transform: translateX(-100%); }
    100% { transform: translateX(100%); }
}

/* Options Styles Start */

.options-wrapper {
  padding: 1em;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  min-height: 5vh;
}

.options-btn-wrapper {
  float: right;
  position: relative;
}
  
.options-btn-wrapper hr {
  background-color: rgba(0,0,0,0.3);;
  margin: 0.4em 0;
}
  
.options-btn {
  position: relative;
  width: 2em;
  height: 2em;
  z-index: 3;
}

.options-btn::after {
  background-color: rgba(255,255,255,0.3);
  border-radius: 50%;
  content: '';
  opacity: 0;
  position: absolute;
  left: 0;
  transition: 0.3s ease;
  top: 0;
  width: 100%;
  height: 100%;
}

.options-btn:hover::after { opacity: 1; }
  
.options-icon {
  color: #fff;
  font-size: 1.5em;
  pointer-events: none;
}
  
.options-list {
  background-color: #fff;
  border-radius: 5px;
  color: #333;
  padding: 0.5em 1em;
  position: absolute;
  top: 120%;
  right: 50%;
  text-align: center;
  width: 22vmax;
  max-width: 8em;
  height: 14vmax;
  max-height: 5em;
  z-index: 3;
  transition: 0.3s ease;
}
  
.options-list.hide {
  opacity: 0;
  transform: translateY(-10px);
}
  
.options-list.show {
  -webkit-animation: show-options 0.3s ease;
          animation: show-options 0.3s ease;
}
  
@-webkit-keyframes show-options {
  0% {
    opacity: 0;
    transform: translateY(-10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
  
@keyframes show-options {
  0% {
    opacity: 0;
    transform: translateY(-10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
  
.options-list button { transition: 0.3s ease; }
.options-list button:hover { color: #999; }

.options-list label {
  margin: 0 0.5em;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.options-list input[type='radio'] {
  cursor: pointer;
}
  
.overlay,
.updating-screen {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  z-index: 2;
}
  
.overlay { transform: translateX(100%); }

.overlay.show { transform: translateX(0); }

.updating-screen {
  background-color: rgba(0,0,0,0.7);
  flex-direction: column;
  z-index: 5;
}

.updating-screen img {
  -webkit-animation: pulse 0.8s ease-in-out infinite;
          animation: pulse 0.8s ease-in-out infinite;
  width: 5em;
}

@-webkit-keyframes pulse {
  0% { transform: scale(1);}
  30% { transform: scale(1.2);}
  60% { transform: scale(1);}
  100% { transform: scale(1);}
}

@keyframes pulse {
  0% { transform: scale(1);}
  30% { transform: scale(1.2);}
  60% { transform: scale(1);}
  100% { transform: scale(1);}
}

.updating-screen p {
  font-size: 1.2em;
  letter-spacing: 1px;
  margin: 0;
}

.loading-dots div {
  -webkit-animation: loading-dot 1.5s infinite;
          animation: loading-dot 1.5s infinite;
  background-color: #fff;
  border-radius: 50%;
  margin-right: 0.5em;
  opacity: 0;
  width: 1em;
  height: 1em;
}

.loading-dots div:nth-child(2) { -webkit-animation-delay: 0.33s; animation-delay: 0.33s; }
.loading-dots div:nth-child(3) { -webkit-animation-delay: 0.67s; animation-delay: 0.67s; }

@-webkit-keyframes loading-dot {
  0% { opacity: 0; transform: scale(0); }
  33% { opacity: 1; transform: scale(1); }
  67%,100% {opacity: 0; transform: scale(0); }
}

@keyframes loading-dot {
  0% { opacity: 0; transform: scale(0); }
  33% { opacity: 1; transform: scale(1); }
  67%,100% {opacity: 0; transform: scale(0); }
}

/* Options Styles End */
/* Summary Styles Start */

.summary-wrapper {
    flex-direction: column;
    min-height: 40vh;
    position: relative;
  }
  
  .location {
    font-size: 1.7em;
    font-weight: 500;
    letter-spacing: 1px;
  }
  
  .location-icon {
    color: #fff;
    font-size: 1.7em;
