  }
  
  .current-temp-deg { font-size: 3em; }
  
  .current-maxmin { margin-bottom: 0.5em; }
  
  .current-maxmin,
  .current-condition { font-size: 1.2em; }
  
  .api-update-wrapper.flex-center {
    justify-content: space-between;
    position: absolute;
    bottom: 0;
    margin-bottom: -0.7em;
    width: 100%;
  }
  
  .darksky-link,
