    color: #bbb;
    font-size: 0.8em;
    transition: all 0.3s ease;
  }
  
  .darksky-img {
    -webkit-filter: invert(70%);
            filter: invert(70%);
    height: 1em;
    margin-right: 0.3em;
    transition: -webkit-filter 0.3s ease;
    transition: filter 0.3s ease;
    transition: filter 0.3s ease, -webkit-filter 0.3s ease;
  }
  
  .darksky-link:hover .darksky-img { -webkit-filter: invert(100%); filter: invert(100%); }
  
  /* Summary Styles End */
/* Comfort Level Styles Start */

.comfort-wrapper {
    margin: auto;
    width: 100%;
  }
 
  .humidity,
  .comfort-info { flex-direction: column; }
 
  .humidity-bar-wrapper { position: relative; }
 
  .humidity-value {
    font-size: 1.7em;
    position: absolute;
  }
 
  .humidity-value::after { content: '%'; }
 
  .humidity-bar {
    width: 8em;
    height: 8em;
  }
 
  .humidity-bar-inner,
  .humidity-bar-outer {
    fill: none;
    stroke: rgba(255, 255, 255, 0.3);
    stroke-dasharray: 22em 22em;
    stroke-dashoffset: 3.7em;
    stroke-linecap: round;
    stroke-width: 0.5em;
    transform-origin: 50% 50%;
    transform: rotate(120deg);
    transition: 1s linear;
  }
 
  .humidity-bar-inner {
    stroke: #fff;
    stroke-dashoffset: 22em;
  }
 
 .humidity-bar-level {
   color: var(--accent);
   margin-top: -0.5em;
 }
 
 .comfort-info.flex-center {
   padding: 0.0.5em;
   align-items: stretch;
 }
 
 .data_title,
 .data_value,
 .humidity-bar-level {
   font-size: 0.9em;
   font-weight: 300;
 }
 
 .data_title {
   color: var(--accent);
   text-align: right;
 }
 
 .data_title,
 .data_value { padding: 0 0.5em; }
 
 .data_value {
   padding-right: 0;
   word-break: break-all;
 }
 
   /* Comfort Level Styles End */
.windmill {
    font-size: 1em;
    position: absolute;
    width: 8em;
    height: 8em;
    bottom: 0;
    transform: translateX(-50%);
}

.base {
    border-left: 0.25em solid transparent;
    border-right: 0.25em solid transparent;
    border-bottom: 5.1em solid #fff;
    border-radius: 0 0 0.5em 0.5em;
    height: 0;
    width: 0.6em;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
}

.rotor {
    background-color: #fff;
    border-radius: 50%;
    height: 0.5em;
    width: 0.5em;
    position: absolute;
    bottom: 5.4em;
    left: 50%;
    transform: translate(-50%, 50%);
}

.blades {
    -webkit-animation: spin-blades 5s linear infinite;
            animation: spin-blades 5s linear infinite;
    cursor: pointer;
    height: 7.5em;
    width: 7.5em;
    position: absolute;
    bottom: 5.4em;
    left: 50%;
    transform: translate(-50%, 50%);
    transition: 0.3s ease;
}

.blades:hover { -webkit-animation-duration: 1s; animation-duration: 1s; }

.blade {
    border: 0.28em solid transparent;
    border-right: 1.8em solid #fff;
    position: absolute;
    left: 50%;
    top: 50%;
    height: 0;
    width: 0;
    transform-origin: 0;
    transform: translateY(-50%);
}

.blade::after {
    border: 0.28em solid transparent;
    border-left: 1.8em solid #fff;
    content: '';
    position: absolute;
    left: 1.78em;
    top: -0.28em;
    height: 0;
    width: 0;
}

.blade:nth-child(1) {
    transform: translateY(-50%) rotate(0deg);
}

.blade:nth-child(2) {
    transform: translateY(-50%) rotate(120deg);
}

.blade:nth-child(3) {
    transform: translateY(-50%) rotate(240deg);
}

@-webkit-keyframes spin-blades {
    0% { transform: translate(-50%, 50%) rotate(0deg); }

    100% { transform: translate(-50%, 50%) rotate(360deg); }
}

@keyframes spin-blades {
    0% { transform: translate(-50%, 50%) rotate(0deg); }

    100% { transform: translate(-50%, 50%) rotate(360deg); }
}

/* Wind Styles Start */

.wind-wrapper {
    margin: auto;
    width: 100%;
  }
  
  .windmill-wrapper {
     min-height: 8em;
     padding: 0 0.5em;
     position: relative;
   }
  
  .wind-info.flex-center {
    align-items: stretch;
    flex-direction: column;
  }
  
   /* Wind Styles End */
/* Sun and Moon Styles Start */

.sunrise-sunset-wrapper {
    margin-bottom: -1rem;
    overflow: hidden;
  }
  
  .sun-path-wrapper {
    font-size: 11px;
    margin: auto;
    position: relative;
    width: 20em;
    height: 10em;
  }
  
  .sun-path {
    position: absolute;
    top: 0;
    width: 100%;
    height: 20em;
  }
  
  .sun-path-full,
  .sun-path-current {
    fill: none;
    stroke: rgba(255, 255, 255, 0.3);
    stroke-width: 2px;
    stroke-dasharray: 0.5em 0.4em;
    stroke-dashoffset: 30em;
    transform-origin: 50% 50%;
    transform: rotate(180deg);
  }
  
  .sun-path-current {
    stroke: #ffd800;
    stroke-dasharray: 60em 60em;
    stroke-dashoffset: 60em;
    transition: 1.5s linear;
  }
  
  .sun-wrapper {
    position: absolute;
    bottom: -9.5em;
    height: 19em;
    width: 19em;
    transition: 1.5s linear;
  }
  
  .sun {
    cursor: pointer;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translate(-50%, -50%);
    transition: 0.5s ease;
    width: 1.7em;
  }
  
  .sun:hover {
    transform: translate(-50%, -50%) rotate(90deg) scale(1.5);
  }
  
  .sunrise-time,
  .sunset-time,
  .lunar-phase-wrapper {
    color: var(--accent);
    font-size: 0.9em;
    font-weight: 300;
    position: absolute;
    bottom: 0;
  }
  
  .sunrise-time { left: 0; transform: translateX(-140%); }
  .sunset-time { right: 0; transform: translateX(140%);}
  .lunar-phase-wrapper { left: 50%; transform: translateX(-50%); }
  
  .lunar-phase-wrapper span { margin-left: 0.2em; }
  
  .lunar-icon {
    color: var(--accent);
    font-size: 1.5em;
  }
  
  /* Sun and Moon Styles End */
