/* Error Styles Start */

.error-wrapper {
  position: absolute;
  font-size: 16px;
  width: 100%;
  height: 100vh;
  z-index: 5;
}
  
.error-wrapper.hide {
  animation: hide-error 0.5s ease-out forwards;
}

@keyframes hide-error {
  0% {
    opacity: 1;
    transform: translate(0);
  }
  100% {
    opacity: 0;
    transform: translate(100%);
  }
}

.error-bg {
  background-color: rgba(0,0,0,0.7);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  z-index: 1;
}

.error-cloud-wrapper {
  background-color: #8C1BAB;
  background-image: radial-gradient(circle, #F761A1 10%, #8C1BAB 60%);
  border-radius: 0.5em;
  flex-direction: column;
  overflow: hidden;
  padding: 1em;
  position: absolute;
  text-align: center;
  width: 90%;
  height: 90%;
  z-index: 2;
}

.error-heading {
  position: relative;
  letter-spacing: 2px;
}

.error-heading h2 {
  font-size: 4em;
  font-weight: 800;
}

.error-heading .error-sign {
  background-color: #fff;
  border-radius: 0.05em;
  color: #8C1BAB;
  font-weight: 800;
  position: absolute;
  top: 100%;
  transform: rotate(7deg) translateY(-50%);
  width: 100%;
  height: 2rem;
}

.error-cloud-wrapper img { width: 16em; }

.error-cloud-wrapper p { margin: 0; }

.error-cloud-wrapper button {
  border: 0.2em solid #4AB5ED;
  border-radius: 100vmax;
  color: #4AB5ED;
  font-family: inherit;
  font-weight: 500;
  letter-spacing: 1px;
  margin-top: 1em;
  text-transform: uppercase;
  transition: background-color 0.3s, color 0.3s;
  width: 7em;
  height: 2em;
}

.error-cloud-wrapper button:hover {
  border: none;
  background-color: #4AB5ED;
  color: #333;
}

@media (min-width: 768px) {
  .error-cloud-wrapper {
    max-width: 35em;
    max-height: 35em;
  }
}

  /* Error Styles End */